import React from 'react'
import { useFilterContext } from '../context/filter_context'
import { BsFillGridFill, BsList } from 'react-icons/bs'
import styled from 'styled-components'
import { useProductsContext } from '../context/products_context'
const Sort = () => {
  const {
    filtered_products: products,
    sort,
    grid_view,
    setGridView,
    setListView,
    updateSort,
  } = useFilterContext()
  const { products_loading: loading } = useProductsContext()
  return <Wrapper>
    {
      !loading
      && <>
        <div className="btn-container">
          <button onClick={setGridView} className={`${grid_view ? 'active' : null}`} type="button">
            <BsFillGridFill />
          </button>
          <button onClick={setListView} className={`${!grid_view ? 'active' : null}`} type="button">
            <BsList />
          </button>
        </div>
        <p>{products.length} products found</p>
        <hr />
        <form>
          <label htmlFor="sort">Sort By</label>
          <select name="sort" id="sort" className="sort-input"
            value={sort} onChange={(e) => updateSort(e.target.value)}>
            <option value="price-lowest">Price(lowest)</option>
            <option value="price-highest">Price(Highest)</option>
            <option value="name-a">Name(A-Z)</option>
            <option value="name-z">Name(Z-A)</option>
          </select>
        </form>
      </>}
  </Wrapper>
}

const Wrapper = styled.section`
  display: grid;
  grid-template-columns: auto auto 1fr auto;
  align-items: center;
  margin-bottom: 2rem;
  column-gap: 2rem;
  @media (max-width: 576px) {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 0.75rem;
    .btn-container {
      width: 50px;
    }
    label {
      display: inline-block;
      margin-right: 0.5rem;
    }
  }
  @media (min-width: 768px) {
    column-gap: 2rem;
  }
  p {
    text-transform: capitalize;
    margin-bottom: 0;
  }

  .btn-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 0.5rem;
    button {
      background: transparent;
      border: 1px solid var(--clr-black);
      color: var(--clr-black);
      width: 1.5rem;
      border-radius: var(--radius);
      height: 1.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      svg {
        font-size: 1rem;
      }
    }
    .active {
      background: var(--clr-black);
      color: var(--clr-white);
    }
  }

  .sort-input {
    border-color: transparent;
    font-size: 1rem;
    text-transform: capitalize;
    padding: 0.25rem 0.5rem;
  }
  label {
    font-size: 1rem;
    text-transform: capitalize;
  }
`

export default Sort
